import React, { useEffect, useState } from 'react';
import TronWeb from 'tronweb';
import HelloWorldContract from './build/contracts/HelloWorld.json';

const App = () => {
  const [contract, setContract] = useState(null);
  const [account, setAccount] = useState('');
  const [message, setMessage] = useState('');
  const [newMessage, setNewMessage] = useState('');

  useEffect(() => {
    const init = async () => {
      if (typeof window.tronWeb !== 'undefined') {
        if (window.tronWeb.ready) {
          const fullNodeUrl = 'https://api.trongrid.io';
          const solidityNodeUrl = 'https://api.trongrid.io';
          const eventServerUrl = 'https://api.trongrid.io';
          const tronWebInstance = new TronWeb(fullNodeUrl, solidityNodeUrl, eventServerUrl);
          const networkId = await tronWebInstance.trx.getNetwork();
          const deployedNetwork = HelloWorldContract.networks[networkId];
          const contractInstance = await tronWebInstance.contract().at(deployedNetwork.address);
          setContract(contractInstance);
          const accounts = await tronWebInstance.trx.getAccounts();
          setAccount(accounts[0]);
        } else {
          console.error('TronWeb is not ready');
        }
      } else {
        console.error('TronWeb is not available');
      }
    };

    init();
  }, []);

  const handleGetMessage = async () => {
    try {
      const message = await contract.methods.getMessage().call();
      setMessage(message);
    } catch (error) {
      console.error(error);
    }
  };

  const handlePostMessage = async () => {
    try {
      await contract.methods.postMessage(newMessage).send({ from: account });
      setNewMessage('');
      await handleGetMessage();
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <div>
      <h1>Hello World Smart Contract</h1>
      <p>Connected Account: {account}</p>

      <div>
        <button onClick={handleGetMessage}>Get Message</button>
        <p>Current Message: {message}</p>
      </div>

      <div>
        <input
          type="text"
          placeholder="Enter new message"
          value={newMessage}
          onChange={(e) => setNewMessage(e.target.value)}
        />
        <button onClick={handlePostMessage}>Post Message</button>
      </div>
    </div>
  );
};

export default App;
